
//import logo from './logo.svg';
import './App.css';
import ScrollToTop from 'react-router-scroll-top'
import Toolbar from './components/Toolbar/Toolbar';
import React, { Component } from 'react';
import SideDrawer from './components/SideDrawer/SideDrawer';
import Backdrop from './components/Backdrop/Backdrop';
import { HashRouter as Router, Route, Switch } from 'react-router-dom';
import Ocean_Aquaticlife from './components/Ocean_Aquaticlife/Ocean_Aquaticlife';
import Ocean_Warming from './components/Ocean_Warming/Ocean_Warming';
import Ocean_Overfishing from'./components/Ocean_Overfishing/Ocean_Overfishing';
import Ocean_SeaLevel from './components/Ocean_SeaLevel/Ocean_SeaLevel';
import Ocean_Acidification from './components/Ocean_Acidification/Ocean_Acidification';
import Ocean_Hypoxia from './components/Ocean_Hypoxia/Ocean_Hypoxia';
import Ocean_Plasticpollution from './components/Ocean_Plasticpollution/Ocean_Plasticpollution';
import Ocean_Lionfish from './components/Ocean_Lionfish/Ocean_Lionfish';
import Ocean_Coral from './components/Ocean_Coral/Ocean_Coral';
import Ocean_Seaturtle from './components/Ocean_Seaturtle/Ocean_Seaturtle';
import Ocean_Bivalves from './components/Ocean_Bivalves/Ocean_Bivalves';
import Ocean_Lobster from './components/Ocean_Lobster/Ocean_Lobster';
import Ocean_Flatworm from './components/Ocean_Flatworm/Ocean_Flatworm';
import Ocean_Invasivespecies from './components/Ocean_Invasivespecies/Ocean_Invasivespecies';
import Ocean_Phytoplankton from './components/Ocean_Phytoplankton/Ocean_Phytoplankton';
import Ocean_Zooplankton from './components/Ocean_Zooplankton/Ocean_Zooplankton';
import Ocean_Lanternfish from './components/Ocean_Lanternfish/Ocean_Lanternfish';
import Ocean_Crabs from './components/Ocean_Crabs/Ocean_Crabs';
import Ocean_Jellyfish from './components/Ocean_Jellyfish/Ocean_Jellyfish';
import Ocean_Juvenile from './components/Ocean_Juvenile/Ocean_Juvenile';
import Ocean_Seagrass from './components/Ocean_Seagrass/Ocean_Seagrass';
import Ocean_Kelp from './components/Ocean_Kelp/Ocean_Kelp';
import Ocean_Urchins from './components/Ocean_Urchins/Ocean_Urchins';
import Ocean_Krill from './components/Ocean_Krill/Ocean_Krill';

import Land_Arcticicemin from './components/Land_Arcticicemin/Land_Arcticicemin';
import Land_Deforestation from './components/Land_Deforestation/Land_Deforestation';
import Land_Forestfires from './components/Land_Forestfires/Land_Forestfires';
import HomeBg from './components/HomeBg/HomeBg';
import SideMenuHome from './components/SideMenuHome/SideMenuHome';
import Oceans from './components/Oceans/Oceans';
import Land from './components/Land/Land';
import Land_Population from './components/Land_Population/Land_Population';
import Land_Florafauna from './components/Land_Florafauna/Land_Florafauna';
import Land_Agriculture from './components/Land_Agriculture/Land_Agriculture';
import Atmosphere from './components/Atmosphere/Atmosphere';
import Atmosphere_Composition from './components/Atmosphere_Composition/Atmosphere_Composition';
import Atmosphere_CO2 from './components/Atmosphere_CO2/Atmosphere_CO2';

import NearSpace from './components/NearSpace/NearSpace';
import TakeAction from './components/TakeAction/TakeAction';


import Home from './components/Home/Home';





class App extends Component{




  state = {
    sideDrawerOpen:false
  };

drawerToggleClickHandler = ()=> {

  this.setState((prevState) =>{
    return{sideDrawerOpen: !prevState.sideDrawerOpen};
});
};

backdropClickHandler = () => {
    this.setState({sideDrawerOpen:false});
};

  render(){
    
      
    let backdrop;

    if (this.state.sideDrawerOpen){
   
      backdrop= <Backdrop click={this.backdropClickHandler}/>
    }



    return(
      
      <Router>
      <ScrollToTop>
        <body> 
         
          <Toolbar drawerClickHandler={this.drawerToggleClickHandler} />
          <SideDrawer show={this.state.sideDrawerOpen} />
          {backdrop}
          <SideMenuHome />
          <HomeBg/>
          <main className = "contentmain">

          <Switch>
              <Route path= "/" component={Home} exact/>
              <Route path= "/Oceans" component ={Oceans}/>
              <Route path= "/Oceans_Aquatic-life" component ={Ocean_Aquaticlife}/>
              <Route path= "/Oceans_Invasive-species" component={Ocean_Invasivespecies}/>
              <Route path= "/Oceans_Overfishing" component ={Ocean_Overfishing}/>
              <Route path= "/Oceans_Warming" component ={Ocean_Warming}/>
              <Route path= "/Oceans_Sea-Level" component={Ocean_SeaLevel}/>
              <Route path= "/Oceans_Acidification" component={Ocean_Acidification}/>
              <Route path= "/Oceans_Hypoxia" component={Ocean_Hypoxia}/>
              <Route path= "/Oceans_Plastic-pollution" component={Ocean_Plasticpollution}/>
              <Route path= "/Oceans_Lionfish" component={Ocean_Lionfish}/>
              <Route path= "/Oceans_Coral-reefs" component={Ocean_Coral}/>
              <Route path= "/Oceans_Sea-turtle" component={Ocean_Seaturtle}/>
              <Route path= "/Oceans_Bivalves" component={Ocean_Bivalves}/>
              <Route path= "/Oceans_Lobster" component={Ocean_Lobster}/>
              <Route path= "/Oceans_Flatworm" component={Ocean_Flatworm}/>
              <Route path= "/Oceans_Phytoplankton" component={Ocean_Phytoplankton}/>
              <Route path= "/Oceans_Zooplankton" component={Ocean_Zooplankton}/>
              <Route path= "/Oceans_Lanternfish" component={Ocean_Lanternfish}/>
              <Route path= "/Oceans_Crabs" component={Ocean_Crabs}/>
              <Route path= "/Oceans_Jellyfish" component={Ocean_Jellyfish}/>
              <Route path= "/Oceans_Juvenile" component={Ocean_Juvenile}/>
              <Route path= "/Oceans_Seagrass" component={Ocean_Seagrass}/>
              <Route path= "/Oceans_Kelp" component={Ocean_Kelp}/>
              <Route path= "/Oceans_Urchins" component={Ocean_Urchins}/>
              <Route path= "/Oceans_Krill" component={Ocean_Krill}/>

              <Route path= "/Land" component= {Land}/>
              <Route path= "/Land_Population" component={Land_Population}/>
              <Route path= "/Land_Agriculture" component={Land_Agriculture}/>
              <Route path= "/Land_Arctic-ice-minimum" component={Land_Arcticicemin}/>
              <Route path= "/Land_Flora-fauna" component={Land_Florafauna}/>
              <Route path= "/Land_Deforestation" component={Land_Deforestation}/> 
              <Route path= "/Land_Forestfires" component={Land_Forestfires}/> 

              <Route path= "/Atmosphere" component={Atmosphere}/>  
              <Route path= "/Atmosphere_Composition" component={Atmosphere_Composition}/> 
              <Route path= "/Atmosphere_CO2" component={Atmosphere_CO2}/> 

              <Route path= "/Near-Space" component={NearSpace}/>    

              <Route path= "/Take-Action" component={TakeAction}/>  

              


          
          </Switch>

          </main>
        </body>
        </ScrollToTop>
      </Router>
      
      );

  } 
}



export default App;





